/* @flow */

import React from 'react';
import styled from 'styled-components';
import sample from 'lodash/sample';
import ReactAudioPlayer from 'react-audio-player';
import song1 from '../../socialMedia/Music/01_Morpheo.mp3';
import song2 from '../../socialMedia/Music/02_Asher_Quinn_Greensleeves.mp3';
import song3 from '../../socialMedia/Music/03.mp3';
import song4 from '../../socialMedia/Music/04.mp3';
import song5 from '../../socialMedia/Music/05.mp3';
import song6 from '../../socialMedia/Music/06.mp3';
import song7 from '../../socialMedia/Music/07.mp3';
import song8 from '../../socialMedia/Music/08.mp3';
import song9 from '../../socialMedia/Music/09.mp3';

const Container = styled.div`
  position: fixed;
  bottom: 0;
  right: 0;
  width: 100%;
  background: #362c2a;
  padding: 10px;
  box-sizing: border-box;
`;

const StyledReactAudioPlayer = styled(ReactAudioPlayer)`
  width: 100%;
`;


type Props = {};

const songs = [
  song1,
  song2,
  song3,
  song4,
  song5,
  song6,
  song7,
  song8,
  song9,
];

class Component extends React.Component<Props> {
  state = {
    musicIndex: songs.indexOf(sample(songs)),
    songs,
    playing: false,
  };

  render() {
    return (
      <Container>
        <StyledReactAudioPlayer
          src={this.state.songs[this.state.musicIndex]}
          controls
          autoPlay={this.state.playing}
          onEnded={this.onMusicEnded}
          onPlay ={this.onMusicPlay}
          onPause={this.onMusicPause}
        />
      </Container>
    );
  }

  onMusicPlay = () => {
    this.setState({
      playing: true,
    });
    console.log(this.state)
  };

  onMusicPause = () => {
    this.setState({
      playing: false,
    });
    console.log(this.state)
  };

  onMusicEnded = () => {
    if (this.state.musicIndex + 1 >= this.state.songs.length) {
      this.setState({
        musicIndex: 0,
        playing: true,
      });
      console.log(this.state)
    } else {
      this.setState({
        musicIndex: this.state.musicIndex + 1,
        playing: true,
      });
      console.log(this.state)
    }
  }
}

export default Component;



